@media (max-width: 768px) {

    .footer {
        height: 98rem;
    }

    .footer .container {
        width: 100%;
        margin: 0 3.125vw;
    }

    .footer .container .navbar {
        display: block;
    }

    .footer .container .navbar .navbar-brand img {
        width: 45.313vw;
        height: 14.466vw;
    }

    .footer .container .navbar .footer-left {
        margin-top: 6.219vw;
        margin-bottom: 3.775vw;
        width: 28.725vw;

        color: rgb(55, 36, 185);
        font-family: FUNGIS;
        font-size: 3.125vw;
        font-weight: 400;
        line-height: 3.438vw;
        letter-spacing: 0%;
        text-align: left;
    }

    .footer .container .navbar .buttons-container {
        width: 100%;
        height: max-content;
        justify-content: flex-start;
        align-items: center;
        position: relative;
    }

    .footer .container .navbar .buttons-container .navbar-button {
        margin-right: 4.688vw;
    }

    .footer .container .navbar .buttons-container .navbar-button.btn-top .btn {
        width: 47.12px;
        height: 47.12px;
        border-radius: 7.17px;
        background: rgb(54, 38, 166);
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .footer .container .navbar .buttons-container .navbar-button.btn-top {
        margin-right: 0;
        margin-left: auto;
    }

    .footer .container .navbar .buttons-container .navbar-button .btn {
        width: 10.938vw;
        height: 10.938vw;
    }

    .footer .container .underline-container img {
        width: 12.019vw;
        margin: 0 4.094vw;
    }

    .footer .container .footer-content {
        justify-content: center;
    }

    .footer .container .footer-content .footer-right a {
        font-size: 3.75vw;
        line-height: 4.063vw;
        margin: 0 3.125vw;
        margin-top: 4vw;
        margin-bottom: -7vw;
    }
}