@media (max-width: 768px) {
    .header {
        margin-top: 13.766vw;
    }

    .header .container a img {
        width: 20.531vw;
        height: 6.553vw;
    }

    .header .container .navbar .navbar-toggler {
        width: max-content;
        height: max-content;
        border: none;
        background: none;
        cursor: pointer;
    }

    .header .container .navbar .navbar-toggler img {
        width: 100%;
        object-fit: cover;
    }

    .header .container .navbar .navbar-collapse .navbar-nav {
        display: contents;
        width: max-content;
    }

    .header .container .navbar-collapse .header-layers-bg {
        position: absolute;
        z-index: 2;
    }

    .header .container .navbar-collapse .layers-bg-up {
        width: 100%;
        top: -20vw;
        transition: all 0.5s ease-in-out;
    }

    .header .container .navbar-collapse .burger-menu-bg {
        object-fit: cover;
        object-position: 50% 50%;
        height: 100%;
    }

    .header .container .navbar-collapse .layers-bg-down {
        width: 100%;
        bottom: -20vw;
        transition: all 0.5s ease-in-out;
    }

    .header .container .navbar-collapse .close-menu,
    .header .container .navbar .navbar-collapse .navbar-nav .nav-item {
        z-index: 5;
    }

    .header .container .close-menu {
        width: max-content;
        height: max-content;
        top: -6.491vw;
        right: 6.019vw;
        transition: all 0.5s ease-in-out;
    }

    .header .container .close-menu img {
        width: 8.497vw;
        height: 8.497vw;
    }

    .header .container .navbar-collapse.show .layers-bg-up {
        top: -3vw;
    }

    .header .container .navbar-collapse.show .layers-bg-down {
        bottom: 0;
    }
    .header .container .navbar-collapse.show .close-menu {
        top: 11.509vw;
    }
}