@media (max-width: 768px) {
    .accordion-item {
        margin: 3.181vw 0;
        border-radius: 4.931vw;
    }

    .accordion-title {
        width: 100%;
        height: 17.016vw;

        padding: 0 6.044vw;
        border-radius: 4.931vw;
        font-size: 4.375vw;
        line-height: 4.772vw;
    }

    .accordion-title span {
        width: 74.506vw;
    }

    .accordion-title .accordion-button {
        width: 7.156vw;
        height: 7.156vw;
    }

    .accordion-title .accordion-button .obj img {
        width: 1.075vw;
        height: 7.156vw;
    }

    .accordion-title .accordion-button .obj {
        line-height: 0;
    }

    .accordion-content-wrapper {
        width: 93.75vw;
    }

    .accordion-content {
        padding: 3.975vw 19.328vw 7.037vw 6.044vw;
        font-size: 3.5vw;
        line-height: 5.088vw;
    }
}