        border: 0.919vw solid rgb(23, 23, 23);
        border-radius: 22.525vw;
        box-shadow: 0px 0.919vw 0px 1px rgb(23, 23, 23);
        font-size: 4.375vw;
        line-height: 9.688vw;
        background-size: 104%;

        position: relative;
        text-decoration: none;
        color: #fff;
        overflow: hidden;
    }

    .main .main-section .container .main-section-content .main-section-button::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 1.5em;
        height: 100%;
        background-color: rgba(255, 255, 255, 0.6);
        transform: translateX(-4em) skewX(-45deg);
    }

    .main .main-section .container .main-section-content .main-section-button:hover::before {
        animation: move-light 0.5s;
    }

    @keyframes move-light {
        from {
            transform: translateX(-4em) skewX(-45deg);
        }

        to {
            transform: translateX(10em) skewX(-45deg);
        }
    }

