.animated-link{display:inline-block;text-decoration:none}.animated-link span{display:inline-block;transition:all .3s ease;transform-origin:bottom;font-size:1rem}.animated-link:hover span{animation:jump .4s ease forwards;color:#ffd900}@keyframes jump{0%{transform:translateY(0)}50%{transform:translateY(-0.25rem)}100%{transform:translateY(0)}}@media(max-width: 768px){.animated-link span{font-size:11.55vw;line-height:16.772vw;text-align:center;text-underline-position:from-font;-webkit-text-decoration-skip-ink:none;text-decoration-skip-ink:none}.animated-link:hover span{text-shadow:0px 5.91px 0px rgba(122,0,39,.6)}}